.ColorBox{
    width: 20%;
    height: 25%;
    margin: 0 auto;
     display: inline-block;
     position: relative;
     cursor: pointer;
     margin-bottom: -3.5px;
}
 .copy-button{
     width: 100px;
     height: 30px;
     position: absolute;
     display: inline-block;
     top: 50%;
     left: 50%;
     transform: translate(-50%,-50%);
     outline: none;
     background: rgba(255,255,255,0.3);
     font-size: 1rem;
     line-height: 30px;
     text-transform: uppercase;
     border: none;
     color: white;
     opacity: 0;
     cursor: pointer;
 }
.ColorBox:hover .copy-button{
    opacity: 1;
    transition: all 0.5s;
}
.box-content{
    padding: 10px;
    left: 0;
    bottom:  0;
    color: black;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-size: 12px;
    position: absolute;
}
.see-more{
    background: rgba(255,255,255,0.3);
    position: absolute;
    border: none;
    right: 0;
    bottom: 0;
    color: #fff;
    width: 60px;
    height: 30px;
    text-align: center;
    line-height: 30px;


}
.copy-overlay{
    opacity: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    transform: scale(0.1);

    transition: 0.6s ease-in-out;
}
.copy-overlay.show{
    opacity:1;
    overflow: hidden;

    transform: scale(50);
    z-index: 10;
    position: absolute;
}
.copy-msg{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 4rem;
    transform: scale(0.1);
    opacity: 0;
    flex-direction: column;
    color: #fff;
}
.copy-msg.show{
    opacity: 1;
    transform: scale(1);
    z-index: 25;
    transition: all 0.4s ease-in-out;
    transition-delay: 0.3s;
}
.copy-msg h1{
    background: rgba(255, 255, 255, 0.308);
    display: block;
    width: 100%;
    text-align: center;
    margin-bottom: 0
}
